.tariffs {
   background-color: #000;
   padding: 50px 0;

   .container {
      max-width: 1200px;
      padding: 0 15px;
      margin: 0 auto;
   }

   &__hat {
      display: flex;
      align-items: center;
      gap: 30px;
      padding-bottom: 60px;

      h2 {
         max-width: 390px;
      }

      p {
         max-width: 270px;
         color: #6F6F6F;
         border-left: 3px solid #DF1D63;
         padding: 3px 0 3px 25px;
      }
   }

   &__content {
      display: flex;
      justify-content: space-around;
   }

   &__rate {
      display: flex;
      flex-direction: column;
      gap: 40px;
      padding: 30px 40px;

      &:nth-of-type(1) {
         background: linear-gradient(rgba(0, 0, 0, 0.8), rgba(0, 0, 0, 0.8)), url(../../../../static/images/planet1.jpg) no-repeat center center / cover;
      }

      &:nth-of-type(2) {
         background: linear-gradient(rgba(0, 0, 0, 0.3), rgba(0, 0, 0, 0.3)), url(../../../../static/images/planet2.jpg) no-repeat center center / cover;

         h3 {
            position: relative;

            &::after {
               content: "Popular!";
               color: #515151;
               font-family: "Roboto", sans-serif;
               font-size: 13px;
               font-weight: 400;
               letter-spacing: normal;
               line-height: normal;
               position: absolute;
               top: 10px;
               left: 165px;
            }
         }
      }

      &:nth-of-type(3) {
         background: linear-gradient(rgba(0, 0, 0, 0.7), rgba(0, 0, 0, 0.7)), url(../../../../static/images/planet3.jpg) no-repeat center center / cover;
      }
   }

   &__offers {
      display: flex;
      flex-direction: column;
      gap: 9px;

      p {
         color: #D6D6D6;

         span {
            color: #DF1D63;
         }
      }

      .close {
         color: rgba(111, 111, 111, 0.6);
      }
   }
}

@media screen and (max-width: 1199px) {
   .tariffs {
      .container {
         padding: 0 30px;
      }

      &__rate {
         gap: 30px;
         padding: 20px;

         &:nth-of-type(2) {
            h3 {
               &::after {
                  left: 150px;
               }
            }
         }
      }
   }
}

@media screen and (max-width: 767px) {
   .tariffs {
      .container {
         padding: 0 15px;
      }

      &__hat {
         text-align: center;
         flex-direction: column;
         padding-bottom: 30px;
      }

      &__content {
         flex-direction: column;
         align-items: center;
         gap: 50px;
      }

      &__rate {
         padding: 0 40px;
      }
   }
}